.white-gradient {
	position: absolute;
	width: 20rem;
	height: 20rem;
	/* background: rgba(128, 26, 154, 0.522); */
	/* background: rgba(53, 200, 200, 1); */
	/* background: linear-gradient(43deg, #4158d0 0%, #c850c0 46%, #ffcc70 100%); */
	background: rgb(244, 244, 242);
	background: linear-gradient(
		90deg,
		rgba(244, 244, 242, 1) 0%,
		rgba(233, 131, 14, 1) 0%,
		rgba(226, 100, 50, 1) 42%,
		rgba(242, 0, 121, 1) 75%,
		rgba(53, 200, 200, 1) 100%
	);
	filter: blur(120px);
	border-radius: 100px;
}
